<template>
  <div class="hello">
     <el-header style="line-height: 58px;background: #3190e8;color:white;font-size:20px;font-weight:bold;">
        <div class="right">
        <div class="header"><img style="width:45px;height:45px;vertical-align: middle;margin-right:10px;" src="../assets/111.png"/><span style="vertical-align: middle;">郑州市公安局交通警察支队项目资产管理系统</span></div>
        <el-dropdown trigger="click">
        <i class="el-icon-setting" style="font-weight:bold;"><span style="vertical-align: middle;margin-left:5px;padding:20px 0;">王小虎</span></i>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>个人中心</el-dropdown-item>
          <el-dropdown-item>用户管理</el-dropdown-item>
          <el-dropdown-item>
            <span style="display:inline-block;width:100%;height:100%;" @click="luyou('Login')">退&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;出</span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      </div>
      </el-header>
    <el-container :style="styleobj">
  <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
      <el-menu
      :default-active=$route.path
      :unique-opened=true
      :router=true
      class="el-menu-vertical-demo">
      <!-- 标题名 -->
      <el-menu-item-group>
        <span slot="title" style="font-size:15px;">资产管理</span>
      </el-menu-item-group>
      <!-- 循环列表 -->
      <el-submenu :index=item.name v-for="(item,index) in isdata[0]" :key="index">
        <template slot="title">
          <i :class="'iconfont '+item.icon"></i>
          <span>{{item.name}}</span>
        </template>
          <el-menu-item :index=items.index v-for="(items,indexs) in item.children" :key="indexs">{{items.name}}</el-menu-item>
      </el-submenu>
      <!-- 标题名 -->
      <el-menu-item-group>
        <span slot="title" style="font-size:15px;">项目管理</span>
      </el-menu-item-group>

      <el-submenu :index=item2.name  v-for="(item2,index) in isdata[1]" :key="index+'2'">
        <template slot="title">
          <i :class="'iconfont '+item2.icon"></i>
          <span>{{item2.name}}</span>
        </template>
          <el-menu-item :index=items2.index v-for="(items2,indexs) in item2.children" :key="indexs">{{items2.name}}</el-menu-item>
      </el-submenu>
    </el-menu>
    </el-aside>
<!-- 右侧内容栏 -->
  <el-container>
    <el-main>
      <transition name="fade" mode="out-in">
      <router-view></router-view>
      </transition>
    </el-main>
  </el-container>
</el-container>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import luyou from '../luyou/luyou'
export default {
 name:'index',
 data() {
      return {
        styleobj:{border:'1px solid #eee',height:''},//页面的动态高度随页面可视高度来定
        // listarr:
      }
    },

    computed:{
        ...mapState({//获取vuex里的所有state内的数据
          isdata:state => state.list.listarr
        })
    },
    methods:{
      luyou(zhi){//跳转路由
        this.$router.push({
          name:zhi
        })
      }
    },
    mounted(){
      this.styleobj.height = `${document.documentElement.clientHeight - 60}px`;//获取页面的可视高度
    },

    created(){
      this.$store.dispatch("list/actionlist",luyou)//执行vuex里的方法
    }
}
</script>

<style scoped>
      .fade-enter {
     opacity:0;
    }
    .fade-leave{
      opacity:1;
    }
    .fade-enter-active{
      transition:opacity .3s;
    }
    .fade-leave-active{
      opacity:0;
      transition:opacity .3s;
    }
    .right{
      display: flex;
      justify-content: space-between;
    }
    .right >>> .el-dropdown{
      color: white;
      cursor: pointer;
    }
    .right >>> .el-icon-setting:before{
      font-size: 18px;
      vertical-align: middle;
    }
    .el-dropdown-menu >>> .el-dropdown-menu__item{
      padding: 0;
      text-align: center;
      width: 96px;
    }
    #app .el-main{
      padding-top: 0;
    }
</style>



